.offers{
    width: 65%;
    height: 60vh;
    display: flex;
    margin: auto;
    padding: 0px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg, #fde1ff 0%, #e1ffea22 60%);
} 
 .offers_left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
 .offers_left h1{
    color: #171717;
    font-size: 50px;
    font-weight: 600;
}
.offers_left p{
    color: #171717;
    font-size: 18px;
    font-weight: 600;
}
.offers_left button{
    width: 280px;
    height: 50px;
    border-radius: 35px;
    background: #ff4141;
    border: none;
    color:white;
    font-size: 18px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
}
.offers-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 50px;

}