.descriptionbox{
    margin: 80px 150px;

}
.descriptionbox_navigator{
    display: flex;
}
.descriptionbox_nav_box{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    font-weight: 600;
    width: 161px;
    height: 60px;
    border: 1px solid #d0d0d0;
}
.descriptionbox_description p{
    font-size: 16px;
}
.descriptionbox_nav_box.fade{
    background: #FBFBFB;
    color: #555;
}
.descriptionbox_description{
    display: flex;
    flex-direction: column;
    gap: 20px;
    border: 1px solid #D0D0D0;
    padding: 40px;
    padding-bottom:40px ;
}