.loginsignup{
    width: 89.4%;
    height: 70vh;
    background: #fce3f3;
    padding: 80px;
}
.loginsignup_container{
    width: 400px;
    height: 400px;
    background: white;
    margin: auto;
    padding: 40px 60px;
}
.loginsignup_container h1{
    margin: 20px 0px;
}
.loginsignup_fields{
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-top: 20px;
}
.loginsignup_fields input{
    height: 40px;
    width: 100%;
    padding-left: 10px;
    border: 1px solid #c9c9c9;
    outline: #5c5c5c;
    font-size: 13px;
}
.loginsignup_container button{
    width: 410px;
    height: 40px;
    color: white;
    background: #ff4141;
    margin-top: 30px;
    border: none;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
}
.loginsignup_login{
    margin-top: 20px;
    color: #5c5c5c;
    font-size: 12px;
    font-weight: 500;
}
.loginsignup_login span{
    color: #ff4141;
    font-weight: 600;
}
.loginsignup_agree{
    display: flex;
    align-items: center;
    margin-top: 25px;
    gap: 20px;
    color: #5c5c5c;
    font-size: 12px;
    font-weight: 500;
}