.shopcategory_banner{
    display: block;
    margin: 20px 120px;
    width: 82%;
}
.shopcategory_indexsort{
    display: flex;
    margin: 0px 120px;
    justify-content: space-between;
    align-items: center;
}
.shopcategory_sort{
    padding: 10px 20px;
    border-radius: 40px;
    border: 1px solid #888888;
}
.shopcategory_indexsort p span{
    font-weight: 600;
}
.shopcategory_products{
    margin: 20px 60px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    row-gap: 80px;
}
.shopcategory_loadmore{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 100px auto;
    width: 183px;
    height: 49px;
    border-radius: 65px;
    background: #ededed;
    color: #787878;
    font-size: 17px;
    font-weight: 500;
}