.newsletter{
    width: 65%;
    height: 40vh;
    display: flex;
    flex-direction:column ;
    align-items: center;
    justify-content: center;
    margin: auto;
    padding: 0px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg,#fde1ff 0%,#e1ffea22 60%);
    gap: 30px;
}
.newsletter h1{
    color: #0c0202;
    font-size: 40px;
    font-weight: 600;
}
.newsletter p{
    color: #454545;
    font-size:17px;
}
.newsletter div{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: white;
    width:630px ;
    height: 50px;
    border-radius: 50px;
    border: 1px solid #e3e3e3;
}
.newsletter input{
    width: 300px;
    margin-left: 30px;
    border: none;
    outline: none;
    color: #616161;
    font-family: Poppins;
    font-size: 14px;
}
.newsletter div button{
    width: 150px;
    height: 50px;
    border-radius: 80px;
    background: black;
    color: white;
    font-size: 13px;
    cursor: pointer;
}