.productdisplay{
    display: flex;
    margin: 0px 100px;    
}
.productdisplay_left{
    display: flex;
    gap: 12px;
}
.productdisplay_img_list{
    display: flex;
    flex-direction: column;
    gap: 12px;
}
.productdisplay_img_list img{
    height: 140px;
}

.productdisplay_main_img{
    width: 486px;
    height: 600px;
}
.productdisplay_right{
    margin: 0px 50px;
    display: flex;
    flex-direction: column;
}
.productdisplay_right h1{
    color: #3d3d3d;
    font-size: 30px;
    font-weight: 700;
}
.productdisplay_right_stars{
    display: flex;
    align-items: center;
    margin-top: 10px;
    gap: 3px;
    color: #1c1c1c;
    font-size: 16px;
}
.productdisplay_right_prices{
    display: flex;
    margin: 40px 0px;
    font-weight: 700;
    gap: 30px;
    font-size: 24px;
}
.productdisplay_right_price_old{
    color: #818181;
    text-decoration: line-through;
}
.productdisplay_right_price_new{
    color: #ff4141;
}
.productdisplay_right_size h1{
    margin-top: 55px;
    color: #656565;
    font-size: 20px;
    font-weight: 600;
}
.productdisplay_right_sizes{
    display: flex;
    margin: 25px 0px;
    gap: 18px;
}
.productdisplay_right_sizes div{
    padding: 14px 20px;
    background: #fbfbfb;
    border: 1px solid #ebebeb;
    border-radius: 3px;
    cursor: pointer;
}
.productdisplay_right button{
    padding: 20px 40px;
    width: 180px;
    font-size: 13px;
    font-weight: 600;
    color: white;
    background: #ff4141;
    margin-bottom: 40px;
    border: none;
    outline: none;
    cursor: pointer;
}
.productdisplay_right_category{
    margin-top: 10px;
}
.productdisplay_right_category span{
    font-weight: 600;
}