.cartitems{
    margin: 100px 170px;
}
.cartitems hr{
    height: 3px;
    background: #e2e2e2 ;
    border: 0;
}
.cartitems_format_main{
    display: grid;
    grid-template-columns: 0.5fr 2fr 1fr 1fr 1fr 1fr;
    align-items: center;
    gap:70px;
    padding: 20px 0px;
    color: #454545;
    font-size: 15px;
    font-weight: 600;
}
.cartitems_format{
    font-size: 14px;
    margin: 0px 0px 0px 15px;
    font-weight: 500;
}
.carticon_product_icon{
    height: 50px;
}
.cartitems_remove_icon{
    width: 15px;
    margin: 0px 22px;
    cursor: pointer;
}
.cartitems_quantity{
    width: 55px;
    height: 40px;
    border: 2px solid #ebebeb;
    background: #ffffff;

}
.cartitems_down{
    display: flex;
    margin: 80px 0px;
}
.cartitems_total{
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-right: 20px;
    gap: 20px;
}
.cartitems_total_item{
    display: flex;
    justify-content: space-between;
    padding: 12px 0px;
}
.cartitems_total button{
    width: 200px;
    height:40px;
    outline: none;
    border: none;
    background: #ff5a5a;
    color: #fff;
    font-size: 12px;
    font-weight: 600;
    cursor: pointer;
}
.cartitems_promocode{
    flex: 1;
    font-size: 14px;
    font-weight:500;
}
.cartitems_promocode p{
    color: #555;
    margin-left: 100px;
}
.cartitems_promobox{
    width: 300px;
    margin-top: 20px;
    margin-left: 100px;
    height: 40px;
    background: #eaeaea;
}
.cartitems_promobox input{
    border: none;
    outline: none;
    background: transparent;
    font-size: 13px;
    width: 200px;
    height: 30px;
}
.cartitems_promobox button{
    width: 95px;
    height: 40px;
    font-size: 12px;
    background: black;
    color: white;
    cursor: pointer;
}